@import url('https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/variable/pretendardvariable.css');

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  outline: none;
}

html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  overflow-x: hidden;
  font-family: 'Pretendard Variable', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Root layout container replacement */
.app-container {
  width: 100%;
  min-width: 340px;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* =========================
   COMMON MEDIA LOADING
========================= */

.media-placeholder-wrapper {
  position: relative;
  width: 100%;
  background-color: #ffffff;
  overflow: hidden;
  aspect-ratio: 4 / 3;
}

.loading-shimmer {
  background: linear-gradient(
    110deg, 
    #ececec 30%, 
    #f5f5f5 50%, 
    #ececec 70%
  );
  background-size: 300% 100%;
  animation: skeleton-shimmer 2.0s infinite ease-in-out; /* 4.0s -> 2.0s */
}

.media-element {
  width: 100%;
  height: auto;
  display: block;
  opacity: 0;
  transition: opacity 0.3s ease-in;
}

.media-placeholder-wrapper:not(.loading-shimmer) {
  background-color: #ffffff;
  aspect-ratio: auto;
}

.media-placeholder-wrapper:not(.loading-shimmer) .media-element {
  opacity: 1;
}

.home-media-frame {
  width: 100%;
  max-width: 800px; /* 로딩 중 최대 너비 */
  aspect-ratio: 4 / 3; /* 미디어 프레임 비율 고정 */
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  /* max-height 제거: 로딩 중에는 너비 기준 비율 유지를 우선함 */
}

/* 로드가 성공적으로 완료된 경우에만 비율 제한을 해제하고 높이 제한 적용 */
.home-media-frame.is-loaded {
  aspect-ratio: auto;
  max-width: 100%;
  max-height: 80vh; /* 실제 콘텐츠는 화면을 넘어가지 않도록 제한 */
}


/* =========================
   MENU ICON
========================= */

.common-menu-icon {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  cursor: pointer;
  margin-top: 7px; /* 32px 폰트(line-height 38px)의 첫 줄 중앙에 맞춤 */
}

.menu-icon-img {
  width: 24px;
  height: 24px;
  object-fit: contain;
  display: block;
}

